<template>
    <div id="planning">
		
    <b-form @submit="onSubmit">
      <!-- Ligne de la barre de pagination -->
      <b-row class="justify-content-center" style="margin: 5px;">
        <b-col md="2">
          <b-form-input id="rechercheDatePickerInput"
                        type="date"
                        required>
          </b-form-input>
        </b-col>
        <b-col md="2">
          <b-button type="submit" size="md" variant="info" >Rechercher</b-button>
        </b-col>
      </b-row>

      <!-- Pour chaques utilisateurs pour la journée sélectionnée on va afficher des cartes -->
      <!-- TODO : Filtres sur le nombre de cartes, les roles, le type d'absence ou le nombre d'heures travaillés, etc... -->
      <b-card-group v-for="user in myJson.user" v-bind:key="user" deck class="mb-3" style="padding: 25px;">
        <b-card border-variant="dark"
                align="center"
                :header="myJson.user[0].nom + ' ' + myJson.user[0].prenom">
            <p class="card-text">
            </p>
            <br/>
            <!-- <p>
              Journée commencée à {{myJson.user[0].nom.planning[0].heureDeb}} et terminée à {{myJson.user[0].nom.planning[0].heureFin}}
              <br/>
              avec une pause le midi de {{myJson.user[0].nom.planning[0].pauseMidi}}
            </p> -->
        </b-card>
      </b-card-group>
    </b-form>
    </div>
</template>

<script>
export default {
  data() {
    return {
      modif: false,
      myJson: {}
    };
  },
  mounted() {
    // On récupère le localstorage dans la page
    this.myJson = JSON.parse(localStorage.getItem("data"));
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
    }
  }
};
</script>